<template src="./newItem.html"></template>

<script>
    import { db } from '@/db';
    const fStore = db.firestore(); //fStore即可使用

    export default {
        name: 'newItem',
        data(){
            return {
                addTask: true,
                title:"",
                date:"",
                time:"",
                commentText:"",
                star: false,
                complete:false,
            }
        },
        methods:{
            //上傳資料鈕
            addMission(){
                let vm = this
                fStore.collection('todo-List')
                .doc(vm.title)
                .set({
                    'commentText': vm.commentText,
                    'complete':false,
                    'date':vm.date,
                    'star':vm.star,
                    'time':vm.time,
                    'title':vm.title,
                    'dataTitle':vm.title, //for doc Title
                    'open':false,
                })
                .then(() => {
                    vm.dataDelete();
                })
            },
            //取消鈕
            cancel(){
                let vm = this;
                vm.dataDelete();
            },
            //消除資料
            dataDelete(){
                let vm = this;
                vm.addTask = true;
                vm.commentText = '';
                vm.complete = false;
                vm.date = '';
                vm.star = false;
                vm.time = '';
                vm.title = '';
            },
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" src="./newItem.scss"></style>
