import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: () => import('../views/index.vue')
  },
  {
    path: '/progress',
    name: 'progress',
    component: () => import('../views/progress.vue')
  },
  {
    path: '/complete',
    name: 'complete',
    component: () => import('../views/complete.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
